.Modal{
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 10000000;
    pointer-events: all;
    &.Active{
        justify-content: center;
        align-items: center;
        display: flex;
    }
    .Content{
        padding: 10px;
        width: 80%;
        background-color: var(--color-White);
    }
}


